<template>
  <div class="amoutSearchResult">
    <v-container
      fluid
      grid-list-xl
    >
    <v-layout>
        <v-flex
          md6
          tag="h3"
          class="mt-0">
          {{ $ln('額度查詢搜尋結果') }}-{{ $ln('明細') }}
        </v-flex>
        <v-flex
          md6
          class="text-md-right">
          <v-btn
            small
            color="amber darken-3"
            @click="toBack()">
            <v-icon class="mr-2">
              mdi-arrow-left-thick
            </v-icon>
            {{ $ln('返回') }}
          </v-btn>
        </v-flex>
      </v-layout>

      <div
        v-if="loadingStatus"
        class="text-xs-center">
        <MaterialLoadingPage/>
      </div>
      <div v-else>
        <p class="category font-weight-light">{{ $ln('帳號') }}：{{ account }} {{ $ln('明細額度紀錄共 %s 筆', totalItems) }}</p>

        <div class="pt-1">
          <v-data-table
            :headers="tableData.headers"
            :items="tableData.contents"
            :no-data-text="$ln(tableData.showNoData)"
            hide-actions
            class="elevation-1">
            <template
              slot="headerCell"
              slot-scope="{ header }">
              <span>
                {{ header.text ? $ln(header.text) : header.text }}
              </span>
            </template>
            <template
              slot="items"
              slot-scope="{ item }">
              <td
                class="text-xs-center"
                style="min-width: 180px;">{{ item.m_date }}</td>
              <td
                class="text-xs-center"
                style="min-width: 100px;">{{ item.bet_gold }}</td>
              <td class="text-xs-left"><div v-html="item.content"/></td>
              <td class="text-xs-center">{{ item.ip }}</td>
              <td :class="['text-xs-right',
                          {'red--text': parseFloat(item.gold) < 0,
                           'green--text': parseFloat(item.gold) > 0}]">
                {{ item.gold }}
              </td>
              <td class="text-xs-center">
                {{ item.o1 }}
              </td>
              <td class="text-xs-center">
                {{ item.o2 }}
              </td>
            </template>
          </v-data-table>
        </div>
      </div>
    </v-container>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'

export default {
  name: 'AmoutSearchDetails',
  data () {
    return {
      i18n: {},
      // 載入資料列表
      tableData: {
        showNoData: '目前無資料',
        headers: [
          { text: '時間', align: 'center', sortable: false },
          { text: '項目', align: 'center', sortable: false },
          { text: '內容', align: 'center', sortable: false },
          { text: 'IP', align: 'center', sortable: false },
          { text: '金額', align: 'center', sortable: false },
          { text: '原額度', align: 'center', sortable: false },
          { text: '新額度', align: 'center', sortable: false }
        ],
        contents: []
      },
      totalItems: '', // 總筆數
      time_start: this.$route.query.time_start,
      time_end: this.$route.query.time_end,
      account: this.$route.query.account,
      // 頁面載入狀態
      loadingStatus: false
    }
  },
  computed: {
    ...mapGetters({
      getSearchDetailsState: 'report/getSearchDetailsState'
    })
  },
  watch: {
    getSearchDetailsState (payload) {
      this.tableData.contents = payload.result
      // 總筆數
      this.totalItems = payload.result.length
    }
  },
  mounted () {
    this.loadSearchDetails()
  },
  methods: {
    ...mapActions({
      getSearchDetails: 'api/getSearchDetails'
    }),
    // 載入帳務紀錄搜尋結果資料
    loadSearchDetails (page, rowsPerPage) {
      // 載入狀態(開始)
      this.loadingStatus = true
      let params = {
        time_start: this.$route.query.time_start,
        time_end: this.$route.query.time_end,
        lv: this.$route.query.lv,
        account: this.$route.query.account
      }
      this.getSearchDetails(params)
        .then(res => {
          if (Object.prototype.toString.call(res) === '[object Error]' &&
                res.hasOwnProperty('response') &&
                res.response.status !== 200) {
                this.toBack()
            console.error(res.response)
          } else if (res.status === 200) {
            console.log('回傳成功')
          }
          // 載入狀態(結束)
          this.loadingStatus = false
        })
        .catch(err => {
          console.log(err)
          // 載入狀態(結束)
          this.loadingStatus = false
          this.toBack()
        })
    },
    toBack () {
      this.$router.push({
        path: '/manage/report/amout-search/Result',
        query: {
          lv: this.$route.query.lv,
          account: this.$route.query.account,
          time_start: this.$route.query.time_start,
          time_end: this.$route.query.time_end }
      })
    }
  }
}
</script>

